let timer1 = '';
let timer2 = '';
import http from '@/api/http.js'
import {checkPhone,checkPwd,checkCode} from "@/common/common.js"
export default {
	data() {
		return {
			sendMsg1:"获取验证码",
			sendMsg2:"获取验证码",
			isCanSendCode1:true,
			isCanSendCode2:true,
			oldphone:"",
			newphone:"",
			oldcode:"",
			newcode:""
		};
	},
	onLoad() {
		this.oldphone = this.$util.getStorage('phone')
	},
	methods: {
		setCodeInterval1 () {
			//设置验证码重新发送定时器
		    let time = 60;
		    clearInterval(timer1);
		    timer1 = setInterval(() => {
		        time--;
		        this.isCanSendCode1=false;
		        this.sendMsg1=time + "s"
		        if (time <= 0) {
		            this.isCanSendCode1=true;
		            this.sendMsg1="重新获取";
		            clearInterval(timer1);
		        }
		    }, 1000)
		},
		clearCodeInterval(){
		    clearInterval(timer1);
		    this.codemsg="获取验证码";
		},
		sendCode () {
			let data = {
				phone:this.oldphone
			}
			http('/api/Login/send_code','post',data).then(res=>{
				if(res.data.status == '200'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				if(res.data.status == '400'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				console.log(res)
			})
			//发送验证码
		    if (!checkPhone(this.oldphone) ) {
		        return
		    }
		    this.setCodeInterval1();
			/*
			* 发送验证码逻辑
			*/
		},
		setCodeInterval2 () {
			//设置验证码重新发送定时器
		    let time = 60;
		    clearInterval(timer2);
		    timer2 = setInterval(() => {
		        time--;
		        this.isCanSendCode2=false;
		        this.sendMsg2=time + "s"
		        if (time <= 0) {
		            this.isCanSendCode2=true;
		            this.sendMsg2="重新获取";
		            clearInterval(timer2);
		        }
		    }, 1000)
		},
		clearCodeInterval2(){
		    clearInterval(timer2);
		    this.codemsg="获取验证码";
		},
		sendCode2 () {
			let data = {
				phone:this.newphone
			}
			http('/api/Login/send_code','post',data).then(res=>{
				if(res.data.status == '200'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				if(res.data.status == '400'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
				}
				console.log(res)
			})
			//发送验证码
		    if (!checkPhone(this.newphone) ) {
		        return
		    }
		    this.setCodeInterval2();
			/*
			* 发送验证码逻辑
			*/
		},
		submit() {
			if(this.oldcode =='' ||this.newphone == '' || this.newcode ==''){
				uni.showToast({
					title:'不能为空',
					icon:'none'
				})
				return;
			};
			if(!checkPhone(this.newphone)){
				uni.showToast({
					title:'请输入正确手机号',
					icon:'none'
				})
				return;
			};
			if(this.oldcode.length!=5 ||this.newcode.length!=5){
				uni.showToast({
					title:'验证码错误',
					icon:'none'
				})
				return;
			};
			let data = {
				token:uni.getStorageSync('token'),
				code:this.oldcode,
				replace_phone:this.newphone,
				replace_code:this.newcode
			}
			http('/api/user/replacePhone','post',data).then(res=>{
				console.log(res)
				if(res.data.status == '200'){
					this.$util.showToast(res.data.message)
					// uni.setStorageSync('token',res.data.data.token);
					setTimeout(function(){
						uni.reLaunch({
							url:'/pages/index/logon/logon'
						})
					},1000)
				}
				if(res.data.status == '400'){
					uni.showToast({
						title:res.data.message,
						icon:'none'
					})
					return;
				}
			})
			
		}
	}
}
